.modal__form-field {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 30px;
}

.modal__form-field:last-of-type {
  margin: 0 0 43px;
}

@media screen and (min-width:1024px) {
  .modal__form-field:last-of-type {
  margin: 0 0 48px; 
  }
}

